const axios = require("axios");
module.exports = {
    base: "http://127.0.0.1:8083",
    get_files:async function(work_id){
        let resp = await axios.post("/public/get_files/" + work_id)
        return resp.data.data
    },
    get_works:async function(){
        let resp = await axios.post("/public/get_works")
        return resp.data.data
    },
    get_work:async function(work_id){
        let resp = await axios.post("/public/get_work/"+work_id)
        return resp.data.data
    },
    upload:async function(file){
        let resp = await axios.post("/public/upload",file)
        return resp.data
    }
}